.project {
    width: 400px;
    text-decoration: none;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: justify;
    border: 1px solid yellow;
    padding: 10px;
    margin: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
    background-color: #222222;
}

.project > span {
    color: white;
    background-color: #222222;
}

.imageContainer {
    position: relative;
    display: block;
    overflow: hidden;
    border: 1px solid black;
    background-color: #222222;
}

.imageContainer > img {
    background-color: #222222;
    width: 100px;
    height: 100px;
    border: 1px solid yellow;
    transition: 1s;
    display: block;
}

.imageContainer > img:hover {
    transform: scale(1.2);
}

.imageContainer > button {
    position: absolute;
    top: 43%;
    left: 25%;
    opacity: 0.4;
    background-color: white;
}

.imageContainer > button:hover {
    background-color: #969BAA;
    opacity: 0.1;
    transition: 0.2s;
    cursor: pointer;
}

.projectInformation > h3 {
    color: white;
    background-color: #222222;
    text-align: center;
}

.projectInformation {
    background-color: #222222;
}

.projectInformation > button {
    margin: 3px;
    font-weight: 100;
    color: black;
    text-decoration: none;
    padding: 2px;
    border-radius: 1px;
    background-color: #969BAA;
    cursor: pointer;
    transition: 0.2s;
}

.projectInformation > button:hover {
    opacity: 0.1;
}

